/* Tooltips */
.has-tip {
  border-bottom: dotted 1px #d9d9d9;
  color: #292929;
  cursor: help;
  font-weight: 600; }
  .has-tip:hover, .has-tip:focus {
    border-bottom: dotted 1px #171717;
    color: #333; }
  .has-tip.tip-left, .has-tip.tip-right {
    float: none !important; }

.tooltip {
  background: #292929;
  color: #fff;
  display: none;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.25;
  max-width: 300px;
  padding: 0.375rem 0.5rem 0.29808rem;
  position: absolute;
  width: 100%;
  z-index: 1006;
  left: 50%; }
  .tooltip > .nub {
    border: solid 5px;
    border-color: transparent transparent #292929 transparent;
    display: block;
    height: 0;
    pointer-events: none;
    position: absolute;
    top: -10px;
    width: 0;
    left: 5px; }
    .tooltip > .nub.rtl {
      left: auto;
      right: 5px; }
  .tooltip.radius {
    border-radius: 2px; }
  .tooltip.round {
    border-radius: 1000px; }
    .tooltip.round > .nub {
      left: 2rem; }
  .tooltip.opened {
    border-bottom: dotted 1px #171717 !important;
    color: #333 !important; }

.tap-to-close {
  color: #f5f5f5;
  display: block;
  font-size: 1rem;
  font-weight: 400; }

@media only screen {
  .tooltip > .nub {
    border-color: transparent transparent #292929 transparent;
    top: -10px; }
  .tooltip.tip-top > .nub {
    border-color: #292929 transparent transparent transparent;
    bottom: -10px;
    top: auto; }
  .tooltip.tip-left, .tooltip.tip-right {
    float: none !important; }
  .tooltip.tip-left > .nub {
    border-color: transparent transparent transparent #292929;
    left: auto;
    margin-top: -5px;
    right: -10px;
    top: 50%; }
  .tooltip.tip-right > .nub {
    border-color: transparent #292929 transparent transparent;
    left: -10px;
    margin-top: -5px;
    right: auto;
    top: 50%; } }

.form-checklist-container {
  clear: both;
  padding: 0.66667rem 0; }

.form-checklist-body {
  margin-left: 3.75rem;
  margin-right: 2.25rem; }
  .form-checklist-body.ng-enter, .form-checklist-body.ng-leave {
    overflow: hidden;
    transition: max-height 600ms cubic-bezier(0.77, 0, 0.175, 1); }
  .form-checklist-body.ng-enter {
    max-height: 0; }
  .form-checklist-body.ng-leave {
    max-height: 26.92308rem; }
  .form-checklist-body.ng-enter.ng-enter-active {
    max-height: 26.92308rem; }
  .form-checklist-body.ng-leave.ng-leave-active {
    max-height: 0; }
  .form-checklist-body-enter, .form-checklist-body-exit {
    overflow: hidden;
    transition: max-height 600ms cubic-bezier(0.77, 0, 0.175, 1); }
  .form-checklist-body-enter {
    max-height: 0; }
  .form-checklist-body-exit {
    max-height: 26.92308rem; }
  .form-checklist-body-enter-active {
    max-height: 26.92308rem; }
  .form-checklist-body-exit-active {
    max-height: 0; }
  @media (min-width: 551px) {
    .form-checklist-body {
      margin-left: 4.5rem; } }

.form-checklist-header > .form-field {
  margin-bottom: 0; }

.form-checklist-header--selected .form-label {
  font-weight: 700; }
